import 'vux/src/styles/reset.less';
import './login.scss'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Datetime from 'vux/src/components/datetime/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


import Myhead from 'components/head/head.vue'

import Top from 'components/top/top.vue'

import Foot from 'components/foot/foot.vue'

import cookie from 'vux/src/tools/cookie/index.js'


Vue.use(ToastPlugin)

import { checkphone } from 'js/validate.js'

import { fetch, rap } from 'js/fetch.js'
let url = {
postLogin: 'api/login',

}
url = rap(url)

import mixin from 'js/mixin.js'

new Vue({
el: '#body',
data: {
tel: '',
passWord: '',
demo2: true,
value1: [],
list1: [['1', '2', '3']],
address: [],
addressData: ChinaAddressV4Data,
value2: '',
isCheckTel: false,
isCheckPW: false,
},
computed: {
isDisabled() {
if (this.isCheckTel === true && this.isCheckPW === true&&this.passWord!==''&&this.tel!=='') {
return false
} else {
return true
}
}
},
created() {

},
methods: {
checkTel(value) {
this.isCheckTel = checkphone(value)
return {
valid: this.isCheckTel,
msg: ''
}
},
checkPW(value) {
this.isCheckPW = value.length >= 6 && value.length <= 12 ? true : false
return {
valid: this.isCheckPW,
msg: '6-12'
}
},
login() {

fetch('post', url.postLogin, {
phone: this.tel,
password: this.passWord
}).then(res => {
if (res.status >= 200 && res.status <= 300) {
cookie.set('token', res.data.token)
this.$vux.toast.show({
text: '',
type: 'success',
onShow() {

},
onHide() {
window.location.href='./member.html'

}
})
} else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn',
onShow() {

},
onHide() {

}
})
}
})
},
show(index) {
if (this.listIndex > index) {
this.transitionName = 'slide-right'
} else {
this.transitionName = 'slide-left'
}
this.listIndex = index
},
onShow() {

},
onHide() {

},
onChange() {

}
},
components: {
Swiper,
Scroller,
Tab,
TabItem,
Sticky,
Flexbox,
FlexboxItem,
XInput,
Group,
XButton,
Cell,
CheckIcon,
PopupPicker,
XAddress,
Datetime
},
mixins: [mixin]
})



